@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.header {
  width: 100%;
  position: relative;
  background-color: #eee;
}

.button-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
}

.download-option {
  display: -webkit-flex;
  display: flex;
  background-color: #ccc;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

.embed-code,
.download-option {
  position: absolute;
  top: 56px;
  left: 0;
  width: 100%;
  height: calc(100% - 57px);
  background-color: #ccc;
}

.embed-code.hide,
.download-option.hide {
  display: none;
}

.embed-code.show,
.download-option.show {
  display: -webkit-flex;
  display: flex;
}

.embed-code .center-code {
  position: relative;
  top: 10vh;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 40vw;
  height: 60vh;
  padding: 1rem;
  overflow: auto;
}

/* SVG */
.content {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: calc(100% - 96px);
}

svg#logoTMA {
  position: relative;
  /* width: 500px;
  height: 500px;
  hide the svg ...*/
  width: 0;
  height: 0;
  background-color: #fff;
  -webkit-font-smoothing: none;
}

circle.track-percentage {
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: rotate(-90deg) translate(-277px, 17px);
          transform: rotate(-90deg) translate(-277px, 17px);
}

text.text-count {
  font-family: "Heebo", "Arial Black", "Arial Bold", Arial, Helvetica, sans-serif;
  font-weight: bold;
  letter-spacing: -0.03rem;
}
text.text-date {
  font-family: Arial, Helvetica, sans-serif;
}

/*PNG */
.logoPNG {
  width: 100%;
  max-width: 500px;
}
.logoPNG.loading {
  display: none;
}
.logoPNG.load {
  display: block;
}

/* buttons */
button {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex: 1 1;
          flex: 1 1;
  border: 1px solid #fff;
  border-width: 0 1px;
  border-radius: 0;
  padding: 1rem 2rem;
  margin: 0;
  outline: none;
  text-decoration: none;
  background: #eaeaea;
  color: #333333;
  font-weight: 900;
  font-family: "Heebo", Arial, Helvetica, sans-serif;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

button:hover {
  background-color: #ddd;
}

/* menu bar */
.button-container button.show {
  background-color: #ccc;
}

.button-container button:after {
  content: "";
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 24px;
  width: 28px;
  height: 24px;
}
.button-container button.show:after {
  background-image: url(/MARC_tracker_app/static/media/close.b529109f.svg);
  background-size: 20px;
}
.button-container button.hide:after {
  background-image: url(/MARC_tracker_app/static/media/expand_more.1afc4c76.svg);
}

/* expand screen */
.download-option button {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  background-color: #ccc;
  color: #333;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #bbb;
  font-size: 1rem;
  font-weight: 400;
  -webkit-flex: 0 1;
          flex: 0 1;
  text-align: left;
  padding: 9vh 7vw;
}

.download-option button:before {
  content: "";
  background-image: url(/MARC_tracker_app/static/media/file_download.b8e5c5d6.svg);
  background-position: 0 center;
  background-repeat: no-repeat;
  background-size: 24px;
  width: 34px;
  height: 24px;
}
.download-option button:hover {
  background-color: #bbb;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #aaa;
}
/* Button styles end */
.loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Heebo", Arial, Helvetica, sans-serif;
  color: #fff;
  background-color: rgba(0, 102, 153, 0.5);
  -webkit-transform-origin: 0 center;
          transform-origin: 0 center;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  visibility: hidden;
}
.loading.show {
  -webkit-animation: fadeInOut 3s forwards ease-in-out;
          animation: fadeInOut 3s forwards ease-in-out;
  visibility: visible;
}
.loading.hide {
  visibility: hidden;
}

@-webkit-keyframes fadeInOut {
  0% {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  1% {
    opacity: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  20% {
    opacity: 1;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  80% {
    opacity: 1;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  99% {
    opacity: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  100% {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  1% {
    opacity: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  20% {
    opacity: 1;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  80% {
    opacity: 1;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  99% {
    opacity: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
  100% {
    opacity: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
}

